#intro{
    height: calc(100vh-4rem);
    width: 100vw;
    max-width: 75rem;
    margin:0 auto;
    overflow: hidden;
    position:relative;
}

.bg{
    position: absolute;
    right: 900px;
    left: 1000px;
    top: 18rem;
    z-index: -1;
    object-fit: cover;
    height: 300px;
    width: 300px;
}

.introContent{
    height: 100vh;
    width: 100vw;
    padding :2rem;
    font-size: 3rem;
    font-weight: 700;
    display:flex;
    flex-direction: column;
    justify-content: center;
    color: #BEADFA;
}

.introName{
    color: purple;
}

.introPara{
    font-size: medium;
    font-weight: 300;
    letter-spacing: 1px;
}

.hello{
    font-size: 1.75rem;
    font-weight: 100;
}

.btn{
    background: white;
    margin: 1rem 0;
    padding:0.75rem 1.5rem;
    border: none;
    border-radius: 2rem;
}

.btnImg{
    object-fit: cover;
    margin: 0 0.25rem;
    height: 1rem;
}

@media screen and (max-width: 670px){
    .bg{
        position: absolute;
        right: -25rem;
        left: 10rem;
        top: 5rem;
        z-index: -1;
        object-fit: cover;
        height: 150px;
        width: 150px;
    }
    .introContent{
        font-size: 10vw;
    }
    .hello{
        font-size: 4.5vw;
    }
}